@font-face {
  font-family: "YesevaOne-Regular";
  font-display: auto;
  src: url("../fonts/YesevaOne-Regular.ttf") format("opentype");
}

@font-face {
  font-family: "Raleway-Regular";
  font-display: auto;
  src: url("../fonts/Raleway-Regular.ttf") format("opentype");
}

.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
  // @include yor(14);

  &-header{
    display: flex;
    // flex-basis: 30%;
    height: 100vh;
    align-items: center;
    @include mq($q540){
      flex-direction: column-reverse;
      height: auto;
    }
    // .main-logo{
    //   height: 80vh;
    //   display: block;
    //   @include mq($q540){
    //     // transform: rotate(90deg);
    //     display: none;
    //   }
    //   &-mob{
    //     display: none;
        
    //     @include mq($q540){
    //       display: block;
    //     }
    //   }
    // }
    .menu{
      // padding: 44% 0 0 0;
      display: none;
      @include mq($q540){
        display: flex;
      }
      &:hover{
          cursor: pointer;
      }
      &__open{
          position: absolute;
          top: 5%;
          // dn
      }
      &_burger{
          content: "";
          position: absolute;
          width: 38px;
          height: 3px;
          display: block;
          background-color: $base-green;
          &:after{
              content: "";
              position: absolute;
              width: 38px;
              height: 3px;
              display: block;
              background-color: $base-green;
              top: -11px;
          }
          &:before{
              content: "";
              position: absolute;
              width:38px;
              height: 3px;
              display: block;
              background-color: $base-green;
              top: 11px;
          }
         
      }
    }
  }
  &-animation{
    display: flex;
    height: 97vh;
    align-items: center;
    background-size: 100% !important;
    justify-content: center;
    z-index: 99;
    overflow: hidden;
    background-image: url(../img/vector-obj.png);
    background-position: center;
    background-repeat: no-repeat;
    // background-size: 72%;
    @include mq($q16){
      background-size: 90%;
      margin-top: 5.5%;

    }
    @include mq($q14){
      // background-size: 90%;
      background-size: 72%;
      margin-top: 2.7%;
    }
    @include mq($q8) {
      background-size: 105% !important;
      width: 105%;
    }
    img{
      z-index: -1;
      animation: App-logo-spin infinite 60s linear;
      @include mq ($q14){
        height: 120vh;
      }
      @include mq($q8) {
        height: 88vh;
      }
    }
  }
 
}



.col-lg-4, .col-lg-8{
  padding: 0 !important;
}
.container{
  padding: 0!important;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}