//mixins
@import './Mixin.scss';

//variables
@import './Variables.scss';

@import './NavMenu.scss';

.AboutUs{
    width: 100%;
    background: #fff;
    height: 100vh;
    // position: absolute;
    // overflow: hidden;
    display: flex;
    flex-basis: 100%;
    max-width: 1140px;
    margin: 0 auto;
    @include mq($q10){
        flex-direction: column;
    }
    &-Navigation{
        display: flex;
        flex-basis: 50%;
        @include mq($q10){
            -webkit-box-shadow: 1px 2px 5px -3px rgba(74,104,32,1);
            -moz-box-shadow: 1px 2px 5px -3px rgba(74,104,32,1);
            box-shadow: 1px 2px 5px -3px rgba(74,104,32,1);
            position: fixed;
            // flex-basis: 50%;
            background: $base-white;
            width: 100%;
        }
    }
    .BurgerMenu{
        @include mq($q10){
            display: flex;
            justify-content: flex-end;
        }
        &__burger{
           img{
            @include mq($q8){
                width: 90%;
            } 
           }           
        }
    }
    .Main-header{
        @include mq($q10){
            position: relative;
            margin-left: 5%;
        }
        &_logo{
            .main-logo-mob{
                width: 80%;
                @include mq($q7){
                    width: 50%;
                }
                @include mq($q480){
                    width: 65%
                }
                @include mq($q5){
                    width: 80%;
                }
            }
        }
    }
   
    &-header{
        display: flex;
        height: 100vh;
        align-items: center;
    }
    h1{
        color: $base-green;
        @include yor(36, 4);
        font-weight: bold;
    }
    &-description{
        
        height: 100vh;
        // overflow-y: scroll;
        @include mq($q10){
            width: 90%;
            margin: 0 auto;
        }
        &_aboutUs{
            padding: 13% 0;
            height: 100vh;
            
            @include mq($q8) {
                padding: 16% 0;
            }
            @include mq($q480){
                padding: 25% 0;
            }
            @include mq($q4){
                padding: 30% 0;
            }

            &__top{
                width: 42%; 
                text-align: left;
                @include mq($q8){
                    width: 100%;
                }
                p{
                    padding: 6% 0 0 0;
                    margin-bottom: 0;
                    @include rr(14);
                    @include mq($q8){
                        padding: 3.4% 0 0 0;
                    }
                }
            }
            &__bottom{
                display: flex;
                flex-direction: row;
                width: 100%;
                align-items: center;
                text-align: left;

                &:hover{
                    cursor: pointer;
                }
                h1{
                  padding: 4.6% 0 0 0;
                  width: 100%;
                }
                &-yes{
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    align-items: center;

                    //inner-block
                    &__arrow{
                        // position: absolute;
                        margin-top: 1.2%;
                    }
                    &__confirm{
                        // position: absolute;
                        margin: 3% 0 0 8%;
                    }
                }
            }
        }
        &_workOn{
            padding: 150px 0 0 0;
            height: 100vh;
            // text-align: left;
            text-align: left;
            @include mq($q8){
                padding: 6% 0;
                max-width: 600px;
                margin: 0 auto;
                height: auto;
            }

            &__top{
                display: flex;
                // flex-basis: 100%;
                width: 100%;
                flex-direction:row;
                @include mq($q8){
                    width: 100%;
                }
                
                &-left{
                    display: flex;
                    flex-direction: column;
                    width: 41%;
                    margin: 0 22% 0 0;  
                    &__item{
                        display: flex;
                        flex-direction: row;
                        margin-top: 10%;
                        img{
                            position: absolute;
                            margin-left: -27px;
                            margin-top: 5px;
                            height: 15px;
                            // margin-right: 20px;
                        }
                        p{
                            // padding: 6% 0 0 0;
                            margin-bottom: 0;
                            @include rr(14);
                            @include mq($q8){
                                padding: 3.4% 0 0 0;
                            }
                        }
                    }
                }
                &-right{
                    display: flex;
                    flex-direction: column;
                    width: 50%;
                    &__item{
                        display: flex;
                        flex-direction: row;
                        margin-top: 10%;
                        img{
                            position: absolute;
                            margin-left: -27px;
                            margin-top: 5px;
                            height: 15px;
                            // margin-right: 20px;
                        }
                        p{
                            // padding: 6% 0 0 0;
                            margin-bottom: 0;
                            @include rr(14);
                            
                        }
                    }
                }
            }
        }
        &_bestClient{
            padding: 150px 0 0 0;
            height: 100vh;
            text-align: left;
            @include mq($q8){
                padding: 6% 0;
                max-width: 600px;
                margin: 0 auto;
            }
            h1{
                width: 60%;
            }
            &__top{
                display: flex;
                // flex-basis: 100%;
                width: 100%;
                flex-direction:row;
                @include mq($q8){
                    width: 100%;
                }
                &-left{
                    display: flex;
                    flex-direction: column;
                    width: 41%;
                    margin: 0 22% 0 0;  
                    &__item{
                        display: flex;
                        flex-direction: row;
                        margin-top: 10%;
                        img{
                            position: absolute;
                            margin-left: -27px;
                            margin-top: 5px;
                            height: 15px;
                            // margin-right: 20px;
                        }
                        p{
                            // padding: 6% 0 0 0;
                            margin-bottom: 0;
                            @include rr(14);
                            @include mq($q8){
                                padding: 3.4% 0 0 0;
                            }
                        }
                    }
                }
              
            }
            
            &__bottom{
                margin-top: 40px;
                h1{
                    width: 50%;
                }
                display: flex;
                flex-direction: row;
                width: 100%;
                align-items: center;
                &:hover{
                    cursor: pointer;
                }
                h1{
                  padding: 4.6% 0 0 0;
                  width: 100%;
                }
                &-yes{
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    align-items: center;

                    //inner-block
                    &__arrow{
                        position: absolute;
                        margin-top: 1.2%;
                    }
                    &__confirm{
                        position: absolute;
                        margin: 3% 0 0 8%;
                    }
                }
            }
        }
    }
  
}