//mixins
@import './Mixin.scss';

//variables
@import './Variables.scss';


// @import "~slick-carousel/slick/slick.css";
// @import "~slick-carousel/slick/slick-theme.css";
.Services{
    width: 100%;
    background: #fff;
    height: 100vh;
    display: flex;
    flex-basis: 100%;
    max-width: 1140px;
    margin: 0 auto;
    // box-sizing:  content-box ;
    @include mq($q10){
        flex-direction: column;
    }
    @include mq($q8){
        box-sizing: inherit ;
    // left: 0;
    }
    &-Navigation{
        display: flex;
        flex-basis: 40%;
        @include mq($q10){
            -webkit-box-shadow: 1px 2px 5px -3px rgba(74,104,32,1);
            -moz-box-shadow: 1px 2px 5px -3px rgba(74,104,32,1);
            box-shadow: 1px 2px 5px -3px rgba(74,104,32,1);
            position: fixed;
            // flex-basis: 50%;
            background: $base-white;
            width: 100%;
        }
    }
    .BurgerMenu{
        @include mq($q10){
            display: flex;
            justify-content: flex-end;
        }
        &__burger{
           img{
            @include mq($q8){
                width: 90%;
            } 
           }           
        }
    }
    .Main-header{
        @include mq($q10){
            position: relative;
            margin-left: 5%;
        }
        &_logo{
            .main-logo-mob{
                width: 80%;
                @include mq($q7){
                    width: 50%;
                }
                @include mq($q480){
                    width: 65%
                }
                @include mq($q5){
                    width: 80%;
                }
            }
        }
    }

    
    &-description{
        background-image: url(../img/Service-Bg.png);
        background-size: cover;
        height: 100vh;  
        background-position: center;
        background-repeat: no-repeat;
        overflow-y: scroll;

        @include mq($q10){
            padding: 8% 0;
        }

        @include mq($q8) {
            // background-size: 100%;
        }

        h1{
            color: $base-white;
            @include yor(36, 6);
            padding: 63px 0 0 60px;
            text-align: left;
            @include mq($q8){
                // padding: 63px 0 0 123px;

            }
            @include mq($q540){
                // padding: 30% 0 0 8%;
            }
                
        }
        h2{
            @include rr(22);
            font-weight: bold;
            color: $base-white;
        }
        p{
            color: $base-white;
            @include rr(14);
            @include mq($q5){
                padding-right: 15px;
            }
        }
        &_first, &_second, &_third, &_fourth, &_fifth, &_sixth, &_seventh{
            display: flex;
            padding: 30px 0 10px 0;
            @include mq($q10){
                padding: 0;
                height: 70vh;
                align-items: center;
            }
            @include mq($q8) {
                padding: 75px 0 10px 0;
            }
            
            &__left{
                flex-basis: 50%;
                @include mq($q540) {
                    flex-basis: 50%;
                }          
                img{
                    height: 80%;
                }      
                .number-two{
                    margin-left: 25px;
                }
            }
            &__right{
                flex-basis: 46%;
                text-align: left;
                @include mq($q540) {
                    flex-basis: 55%;
                }
                &-details{
                    display: flex;
                    
                    &__arrow{
                        img{
                            height: 12px;
                            margin: -8px 0 0 3px;
                            &:nth-last-child(1){
                                margin-right: 20px;
                            }
                        }
                    }
                                      
                    &:hover{
                        cursor: pointer;
                    }
                }
                
            }
        }
        &_first{
            &__left{
                img{
                    height: 75%;
                    @include mq($q5){
                        height: 293px;
                    }
                }
            }
        }
        &_second{
            &__left{
                @include mq($q5){
                    flex-basis: 50%;
                }
                img{
                    height: 90%;
                }
            }
            &__right{
                @include mq($q5){
                    flex-basis: 49%;
                }
                &-details{
                    height: 10px;
                    padding-top: 44%;
                    @include mq($q8){
                        padding-top: 49%;
                    }
                }
            }
        }
        &_third{
            &__left{
                @include mq($q5){
                    flex-basis: 50%;
                }
            }
            &__right{
                @include mq($q5){
                    flex-basis: 46%;

                }
            }
        }
        &_seventh{
            @include mq($q540){
                padding: 75px 0 75px 0 ;
            }

        }
        // &_second, &_seventh{
        //     display: flex;
        // }

    }
}