//mixins
@import './Mixin.scss';

//variables
@import './Variables.scss';


.Home {
  text-align: center;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
  // @include yor(14);
  overflow: hidden;

  .BurgerMenu{
    display: none;
    @include mq($q7){
      display: block;
    }
  }
  .Main-header{
    &_logo{
      .main-logo-mob{
        @include mq($q540){
          margin-top: 50%;  
        }
      }
    }
  }  
  &-header{
    display: flex;
    // flex-basis: 30%;
    height: 100vh;
    align-items: center;
    // background-color: #fff;
    @include mq($q7){
      position: absolute;
    }
    @include mq($q767){
      position: absolute;
      width: 100%;
    }

    @include mq($q6){
      height: 100%;
      position: relative;
      // flex-direction: column-reverse;
      background-color: #fff;
    }
    // @include mq($q
    &-open{
      display: block;
      width: 100%;
      @include mq($q7){
        // display none;
        display: none;
      }
    }
    &-menu{
      // padding: 44% 0 0 0;
      display: none;
      @include mq($q6){
        display: flex;
        width: 100%;
        height: 56px;
      }
      &:hover{
          cursor: pointer;
      }
      &__open{
          position: absolute;
          top: 5%;
          display: block;
          // dn
      }
      &__burger{
        width: 100%;
        @include mq($q6){
          padding: 32px 0;
        }
      }

    }
  }
  &-animation{
    display: flex;
    height:100vh;
    align-items: center;
    background-size: 145% !important;
    // margin-left: 10%;
    justify-content: center;
    z-index: 99;
    overflow: hidden;
    background-image: url(../img/vector-obj-1.png) !important;
    background-position: center;
    background-repeat: no-repeat;
    @include mq ($q13){
  
      background-size: 145% !important;

    }
    @include mq ($q12){
      background-size: 115% !important;

    }

    @include mq($q8) {
      background-size: 125% !important;
      // width: 105%;
    }
    @include mq($q7){
      // background-image: url(../img/vector-obj.png);
      background-size: 72% !important;

    }
    

    @include mq($q6) {
      background-image: url(../img/vector-obj-1.png);
      background-size: 80% !important;
      margin-top: 6%;

    }
    @include mq($q540) {
      background-size: 125% !important;

    }

    // @include mq($q6) {
    //   width: 100%;
    //   margin-top: -1px;
    //   background-size: 106% !important;

    // }

    // @include mq($q5){
    //   background-size: 105% !important;
    //   width: 102%;
    //   overflow: hidden;
    //   height: 75vh;
    // }

    img{
      z-index: -1;
      animation: App-logo-spin infinite 60s linear;
      width: 100%;
      height: 160vh;  
      @include mq ($q12){
        height: 100vh;
        width: 100%;
      }
      @include mq ($q7){
        height: 90vh;
        width: 100%;
      }
      
    }
  }
}