//mixins
@import './Mixin.scss';

//variables
@import './Variables.scss';

.Contact{
    width: 100%;
    background: $base-green;
    // height: 100vh;
    background-image: url(../img/Contact-Bg.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    &-Description{
        position: absolute;
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        p{
            @include rr(30);
            // text-align: center;
            color: $base-white;
            strong{
                @include rr(30, 1.2);
                text-transform: uppercase;
                font-weight: bold;
            }
        }
        &__Arrow{
            display: flex;
            flex-direction: column;
            margin-top: 10%;
            // width: 50px;
            // height: 50px;
            position: absolute;
            img{
                // height: auto;
                // width: 8px;
                transform: rotate(90deg)
            }
            &:hover{
                cursor: pointer;
            }
        }
    }
    &-Form{
        height: 600px;
        background: $base-white;
        width: 100%;
        padding: 75px 0;
        // position: absolute;
        bottom: 0;
        // display: flex;
        position: absolute;
        // display: flex;
        flex-basis: 100%;
        flex-direction: row;
        &_Fill-Field{
            display: flex;
            flex-basis: 100%;
            &__Navigation{
                display: flex;
                flex-basis: 20%;
                justify-content: center;
            }
            &__Info{
                display: flex;
                flex-basis: 50%;
                justify-content: center;
                p{
                    color: #000000;
                    @include rr(24);
                    font-weight: bold;
                    padding-top: 10px;
                }
            }
            &__Number{
                display: flex;
                flex-basis: 20%;
                a{
                    // text-decoration: none;
                    @include yor(24, 2);
                    color: $base-green;
                    &:hover{
                        color: #69a11b;
                        text-decoration: none;
                    }
                }
            }
        }
    }
}