//mixins
@import './Mixin.scss';

//variables
@import './Variables.scss';


.Main-header{
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    z-index: 1;
    position: fixed;
    @include mq($q10){
        height: unset;
    }    
    @include mq($q8) {
        // padding: 6% 0;
        // position: absolute;
        display: flex;
        // height: 100vh;
    }
    @include mq($q7){
        // display: block !important;
        width: 100%;
        // position: relative;
        align-items: unset;
        // position: absolute;

    }
    @include mq($q6){
        position: relative;
        height: 60px;
        position: fixed;
        background: white;
        width: 100%;
        margin-left: 15px;
    }

    // @include mq($q540){
        // height: 60px;
        // // position: fixed;
        // background: #ffff;
        // width: 101%;
        // padding: 0 0 0 9%;
        // justify-content: flex-start;
    // }
    // @include mq($q5){
    //     justify-content: flex-start;
    //     position: fixed;
    // }

    &_logo{
        @include mq($q991){
            // height: 100vh;
            display: flex;
            align-items: center;
            // position: absolute;
        }
        @include mq($q7){
            // height: 100%;
        }        
        @include mq($q540){
            // width: 400px;
            position: relative;
        }
        .main-logo{
            height: 80vh;
            @include mq($q10){
                // height: 50vh;   
                display: none;                 
            }
            @include mq($q7){
                transform: rotate(90deg);  
                display: none;

            }
        }
        .main-logo-mob{
            display: none;

            @include mq($q10){
                // transform: rotate(-90deg);  
                // height: 55px;
                display: block;
            } 
           
        }
    }
}
