//mixins
@import './Mixin.scss';

//variables
@import './Variables.scss';


.NavMenu{
    display: flex;
    justify-content: flex-end;
    width: 100%;
    // display: block;
    .menu{
        padding: 44% 0 0 0;
        &:hover{
            cursor: pointer;
        }
        &__open{
            position: absolute;
            top: 5%;
            // dn
        }
        &__burger{
            content: "";
            position: absolute;
            width: 38px;
            height: 3px;
            display: block;
            background-color: $base-green;
            &:after{
                content: "";
                position: absolute;
                width: 38px;
                height: 3px;
                display: block;
                background-color: $base-green;
                top: -11px;
            }
            &:before{
                content: "";
                position: absolute;
                width:38px;
                height: 3px;
                display: block;
                background-color: $base-green;
                top: 11px;
            }
           
        }
    }
    @include mq($q540) {
        justify-content: center;
    }
    &_list{
        text-align: left;
        &__item{
            font-weight: bold;
            list-style: none;
            line-height: 25px;
            
            @include mq($q16){
                // @include yor(16, 2.2);
                line-height: 20px;

            }
            @include mq($q16){
                // @include yor(14, 2.2);
            }
            a{
                color: #000;
                @include yor(16, 2.2);

                &:hover{
                    color: $base-green;
                    cursor: pointer;
                    text-decoration: none;
                    &:before{
                        content: "";
                        background-image: url(../img/arrow.png);
                        background-repeat: no-repeat;
                        background-position: center;
                        background-size: 35%;
                        width: 20px;
                        height: 20px;
                        position: absolute;
                        margin-left: -6%;
                        margin-top: 0.5%;
                    }
                }
                &.active{
                    color: $base-green ;
                }
            }
        }
    }
}